import { Component } from 'react';
import { userService } from '../../services/userService';
import './Signup.scss';

export class Signup extends Component {
  state = {
    user: null,
  };

  handleChange = ({ target }) => {
    const field = target.name;
    const value = target.type === 'number' ? +target.value : target.value;
    this.setState((prevState) => ({
      user: { ...prevState.user, [field]: value },
    }));
  };

  onSaveUser = async (ev) => {
    ev.preventDefault();
    await userService.signup(this.state.user);
    this.props.history.push('/contact');
  };

  render() {
    return (
      <section className="flex column align-center main-layout signup-container">
        <form onSubmit={this.onSaveUser}>
          <h3>Please enter your name:</h3>
          <div className="flex align-center space-between">
            <input
              required
              type="text"
              id="name"
              onChange={this.handleChange}
              name="name"
              placeholder="Enter your name"
            />
            <button className="btn">Signup</button>
          </div>
        </form>
        <img src={require('../../assets/imgs/gold-coin.png').default} alt="coin" className="signup-img"/>
      </section>
    );
  }
}
